.ant-tooltip-inner {
  width: fit-content;
}

.tr-content {
  .token {
    margin-right: 4px;
  }

  > p {
    display: flex;
    align-items: center;
    font-family: Avenir Next;
    font-size: 14px;
    line-height: 19px;
    color: #fff;
    margin-bottom: 16px;

    > img {
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }
  }

  > div {
    min-width: 160px;
  }

  ul {
    margin-bottom: 0;
  }

  ul,
  li {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  li {
    margin-right: 6px;

    &:last-child {
      margin-right: 0;

      .tr-arrow {
        display: none;
      }
    }

    > img {
      width: 24px;
      height: 24px;
    }

    > div {
      display: inline-flex;
      align-items: center;
      height: 30px;
      line-height: 30px;
      padding: 0 10px;
      background-color: #fff;
      border-radius: 15px;

      > span {
        font-family: Avenir Next;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        color: #333;
        white-space: nowrap;
      }

      > img {
        width: 16px;
        height: 16px;
        margin-right: 5px;
      }
    }
  }

  .tr-arrow {
    display: inline-block;
    width: 6px;
    height: 10px;
    background: url(../images/arrow-right-white.svg) 0 0 no-repeat;
    background-size: 100%;
    margin-left: 6px;
  }
}
